.headerContainer {
   position: fixed;
   width: 100%;
   z-index: 100;
   .header {
      background: #FCFCFC;
      box-shadow: 0 2px 10px rgba(109, 109, 109, 0.25), inset 0 1px 0 rgba(255, 255, 255, 0.3);
      padding-top: 5px;
      padding-bottom: 5px;
      position: relative;
      .container {
         margin: 0 auto;
         width: 85%;
         display: flex;
         justify-content: space-between;
         .profileData {
            display: flex;
            justify-content: space-between;
            align-items: center;
            gap: 10px;
            .userName {
               font-style: normal;
               font-weight: 500;
               font-size: 16px;
               line-height: 24px;
               display: flex;
               align-items: center;
               color: #000000;
               border-bottom: 1px dashed #000000;
               cursor: pointer;
               text-decoration: none;
            }
            .userPhoto {
               width: 48px;
               height: 48px;
               border-radius: 100%;
               cursor: pointer;
            }
         }
      }
   }
}
