.contentBox {
  display: flex;
  flex-direction: column;
  align-items: center;
  .title {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 347px;
    .cross {
      cursor: pointer;
      transform: rotate(45deg);
      font-size: 40px;
      opacity: 0.8;
      transition: all 400ms;
    }
    .cross:hover {
      transform: rotate(225deg);
    }
  }
  .line {
    width: 395px;
    height: 100%;
    border-top: 1px solid #d9d9d9;
  }
  .form {
    width: 110%;
  }

}


.modal-backdrop {
  opacity: 0 !important;
}